@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&family=Poppins:wght@400;500;600;700;800;900&family=Raleway:wght@300;400;500;600;700;800;900&display=swap);
/* <--- General Styles ---> */

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}
a:link, a:visited, a:active  {
  text-decoration: none;
}
body {
  background-color: #eee;
  /* font-family: 'Lato', sans-serif; */
  font-family: 'Poppins', sans-serif;
  /* font-family: 'Raleway', sans-serif; */
  overflow-x: hidden;
}

/* <--- Section Style ---> */
.section {
    font-family: 'Lato', sans-serif;
    text-align: center;
    width: 100%;
    padding: 30px 20px;
    position: relative;
  }
  
.title-section {
    color: #fff;
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 600;
    margin-top: 10px;
  }
  
.text-section {
    color: #4f89a6;
    font-size: 16px;
    font-weight: 400;
    margin: 10px 0 20px 0;
    text-transform: capitalize;
  }
  
/* <--- ServiceFront Style ---> */
.service-front {
  background-color: #20496b;
  border-radius: 10px;
  cursor: pointer;
  color: #3ADCE2;
  min-height: 137px;
  padding: 10px 5px;
  margin-bottom: 20px;
  text-align: center;
  transition: all 0.4s ease;
  position: relative;
}

.service-front:hover {
  box-shadow: 0 0 20px rgb(58, 220, 226,.65)
}

.service-front .icon {
  border-radius: 50%;
  height: 60px;
  width: 60px;
  display: block;
  margin: 0 auto 20px;
  text-align: center;
  transition: all 0.4s ease;
  position: relative;
}

.service-front .icon i {
  font-size: 40px;
  line-height: 60px;
  transition: all 0.4s ease;
  position: relative;
}

.service-front h4 {
  font-size: 18px;
  font-weight: 700;
  text-transform: capitalize;
  transition: all 0.4s ease;
  position: relative;
}
/* <--- ServiceBack Style ---> */

.service-back {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #3ADCE2;
  box-shadow: 0 0 20px rgb(58, 220, 226,.65);
  border-radius: 5px;
  color: #0f365b;
  cursor: pointer;
  text-align: center;
  min-height: 137px;
  padding: 10px 20px;
  margin-bottom: 20px;
  transition: all 0.4s ease;
  position: relative;
}

.service-back h4 {
  font-size: 18px;
  font-weight: 700;
  text-transform: capitalize;
  transition: all 0.4s ease;
  position: relative;
  margin-bottom: 10px;
}

.service-back p {
  color: #20496B;
  font-size: 15px;
  font-weight: 400;
  position: relative;
}
/* <--- ListOfServices Style ---> */
.list-of-services {
    display: flex;
    flex-direction: column;
    position: relative;
  }
/* <--- ServicesSection Style */
.services {
    background-color: #0f365b;
  }
/* <--- LocationSection Style ---> */
.location {
    background-color: #20496b;
    position: relative;
  }
  
  .location-box {
    background-color: #0f365b;
    border-radius: 10px;
    padding: 20px 15px;
    position: relative;
  }
  
  .box-info {
    border-bottom: 2px solid #20496b;
    margin-bottom: 15px;
    padding-bottom: 10px;
  }
  
  .box-info i {
    display: inline-block;
    font-size: 20px;
    color: #3adce2;
    height: 100%;
    margin-right: 10px;
    position: relative;
    left: 0;
  }
  
  .box-info p {
    font-size: 16px;
    font-weight: 500;
    display: inline-block;
    color: #e8e8e8;
  }
  
  .box-map {
    border-radius: 5px;
    height: 350px;
  }
  
  .iframe-map {
    border: 0;
    border-radius: 5px;
    width: 100%!important;
    margin:0!important;
    height: 100%!important;
  }
  
/* <--- QRCodeSection Style ---> */
.qr-code {
    background-color: #0f365b;
    position: relative;
  }
  
  .qr-code-box {
    background-color: #20496b;
    border-radius: 10px;
    padding: 20px 15px;
  }
  
  .box-web {
    border-bottom: 2px solid #0f365b;
    margin-bottom: 15px;
    padding-bottom: 10px;
  }
  
  .box-web i {
    display: inline-block;
    font-size: 20px;
    color: #3adce2;
    height: 100%;
    margin-right: 10px;
    position: relative;
    left: 0;
  }
  
  .box-web p {
    font-size: 16px;
    font-weight: 500;
    display: inline-block;
    color: #e8e8e8;
    letter-spacing: 1px;
  }
  
  .box-qr {
    border-radius: 5px;
    width: 100%;
  }
  
  .box-qr img {
    border: 0;
    border-radius: 5px;
    width: 100%;
  }
/* <--- CardBody Style ---> */
.card-body {
  width: 100%;
  position: relative;
}

/* <--- CardFooter ---> */
.card-footer {
  position: relative;
  width: 100%;
  max-height: 60px;
  border-top:7px solid #1CC797;
}

.card-footer img {
  background-size: cover;
  border-bottom-left-radius: 18px;
  border-bottom-right-radius: 18px;
  width: 100%;
  height: 100%;
  position: relative;
}

.copyright-text {
  text-align: center;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  border-bottom-left-radius: 18px;
  border-bottom-right-radius: 18px;
  padding-top: 15px;
}
.copyright-text a,
.copyright-text p {
  font-family: 'Raleway', sans-serif;
  font-size: 15.5px;
  font-weight: 500;
  letter-spacing: .5px;
  position: relative;
  color: #fff;
}

/* <--- HeaderBackground Style ---> */

.header-background {
    background-color: #1cc797;
    background-size: cover;
    border-top-left-radius: 18px;
    border-top-right-radius: 18px;
    width: 100%;
    height: 35%;
    position: relative;
}

.header-background img {
    width: 100%;
    max-height: 170px;
    border-top-left-radius: 18px;
    border-top-right-radius: 18px;
}

.background-text {
    color: #fffffe;
    text-align: center;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    border-top-left-radius: 18px;
    border-top-right-radius: 18px;
    padding-top: 15px;
}

.background-text p {
    /* display: none; */
    margin-bottom: 10px;
    font-family: 'Raleway', sans-serif;
    font-size: 20px;
    font-weight: 400;
    letter-spacing: 1px;
}

.background-text span {
    display: block;
    font-family: 'Raleway', sans-serif;
    font-size: 20px;
    font-weight: 400;
    letter-spacing: 1.5px;
}
/* <--- Profile Style ---> */
.profile {
    position: relative;
    width: 100%;
    text-align: center;
}

.profile-img {
    /* box-shadow: 0 0 20px rgba(0, 0, 0, 0.2); */
    background-color: #141837;
    background-size: cover;
    border: 8px solid #fff;
    border-radius: 50%;
    width: 160px;
    height: 160px;
    margin: 0 auto;
    margin-bottom: -80px;
    transform: translateY(-50%);
    text-align: center;
    position: relative;
}

.profile-img img {
    border-radius: 50%;
    width: 100%;
    height: 100%;
}

.profile h1 {
    color: #20496b;
    font-size: 30px;
    font-weight: 400;
    position: relative;
    margin-top: 10px;
}

.profile h2 {
    color: #4f89a6;
    font-weight: 400;
    font-size: 15px;
    position: relative;
    margin-bottom: 5px;
}

.profile p {
    color: #20496b;
    font-weight: 500;
    font-size: 16px;
    position: relative;
    display: block;
}
/* <--- IconLink Style ---> */
.icon-link {
  color: #20496b;
  border-radius: 50%;
  display: inline-block;
  width: 40px;
  height: 40px;
  font-size: 20px;
  margin: 0 5px;
  line-height: 40px;
  text-align: center;
  transition: all 0.3s ease;
}

.icon-link:hover {
  color: #4eedc4;
  background-color: #356484;
}

.icon-link > i {
  transition: all 0.3s ease;
}

/* <--- SocialLinks Style ---> */
.social-links {
  width: 100%;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 40px;
}

/* <--- Contact Style ---> */
:root {
  /* Contact Colors */
  --whatsapp1: #25d366;
  --whatsapp2: #128c7e;
  --messenger1: #00b2ff;
  --messenger2: #006aff;
  /* 0084ff */
  --email1: #ea4335;
  --email2: #bb001b;
  /* EA4335 */
}

.contact {
  position: relative;
  margin: 0 auto;
}

.contact-btn {
  background-color: #4EEDC4;
  border-radius: 25px;
  color: #20496b;
  cursor: pointer;
  border: none;
  bottom: 0;
  right: 50%;
  width: 200px;
  height: 48px;
  margin-bottom: -24px;
  margin-right: -100px;
  position: absolute;
  transition: all 0.4s;
  transition-delay: 0.1s;
  overflow: hidden;
  z-index: 10;
}

.contact-btn:hover {
  transform: scale(1.05);
}

.contact-btn.active {
  /* background-color:#20496B;
    color:#1CC797; */
  margin-right: 0;
  right: 20px;
  width: 65px;
  height: 65px;
  margin-bottom: -32.5px;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  border-radius: 65px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}

.contact-btn span {
  opacity: 1;
  display: block;
  float: left;
  width: 200px;
  padding: 15px 0;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  transition: opacity 0.2s;
  transition-delay: 0.4s;
  letter-spacing: 0.5px;
}

.contact-btn.active span {
  opacity: 0;
  transition-delay: 0s;
  width: 200px;
}

.contact-btn img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: all 0.4s;
}

.contact-btn.active img {
  opacity: 1;
  transition-delay: 0.5s;
}

.contact-title {
  background-color: #20496b;
  width: 100%;
  max-height: 0px;
  float: left;
  overflow: hidden;
  transition: all 0.55s;
  z-index: 5;
  position: relative;
  text-align: left;
}

.contact-title.active {
  max-height: 100px;
  transition: all 1.3s;
}
.contact-title p {
  color: #fff;
  padding: 15px 30px;
  font-weight: 500;
  font-size: 16px;
  display: inline-block;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: all 0.3s;
  transform: scale(0);
}

.contact-title.active p {
  transform: scale(1);
  transition-delay: 0.2s;
}

.contact-list {
  text-align: left;
  background-color: #E9EBED;
  /* float: left; */
  width: 100%;
  overflow: hidden;
  max-height: 0;
  transition: all 0.5s;
  position: relative;
  z-index: 4;
}
.contact-list.active {
  max-height: 600px;
  padding: 12px 20px 30px 20px;
  transition: all 1s;
}

.contact-list.active a {
  background-color: #fff;
  opacity: 1;
  transform: translateY(0px);
  transition: all 0.7s;
}

.contact-list.active a:nth-of-type(1) {
  transition-delay: 0.5s;
}

.contact-list.active a:nth-of-type(2) {
  transition-delay: 0.6s;
}

.contact-list.active a:nth-of-type(3) {
  transition-delay: 0.7s;
}

.contact-list a {
  float: left;
  width: 100%;
  margin-top: 18px;
  padding: 10px;
  border: transparent;
  border-radius: 10px;
  box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.2);
  text-decoration: none;
  transition: all 0.3s;
  opacity: 0;
  transform: translateY(-10px);
  position: relative;
}

.contact-list a .arrow {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  transform: translateY(-50%);
}

.contact-list a.whatsapp .icon img {
  background: linear-gradient(
    to bottom left,
    #25d366,
    #128c7e);
  background: linear-gradient(
    to bottom left,
    var(--whatsapp1),
    var(--whatsapp2)
  );
}

.contact-list a.whatsapp .content h1 {
  background: linear-gradient(#25d366, #128c7e);
  background: linear-gradient(var(--whatsapp1), var(--whatsapp2));
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
}

.contact-list a.messenger .icon img {
  background: linear-gradient(#00b2ff, #006aff);
  background: linear-gradient(var(--messenger1), var(--messenger2));
}

.contact-list a.messenger .content h1 {
  background: linear-gradient(#00b2ff, #006aff);
  background: linear-gradient(var(--messenger1), var(--messenger2));
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
}

.contact-list a.gmail .icon img {
  background: linear-gradient(
    #ea4335,
    #ea4335,
    #ea4335,
    #bb001b);
  background: linear-gradient(
    var(--email1),
    var(--email1),
    var(--email1),
    var(--email2)
  );
}

.contact-list a.gmail .content h1 {
  background: linear-gradient(
    #ea4335,
    #ea4335,
    #ea4335,
    #bb001b);
  background: linear-gradient(
    var(--email1),
    var(--email1),
    var(--email1),
    var(--email2)
  );
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
}

.contact-list a .icon {
  float: left;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 10px;
}

.contact-list a .icon img {
  width: 100%;
  height: 100%;
  padding: 8px;
}

.contact-list a .content h1 {
  font-size: 18px;
  font-weight: 400;
  line-height: 160%;
  letter-spacing: 0.4px;
  margin: 0;
}

.contact-list a .content span {
  font-size: 14px;
  color: #827F7B;
  display: block;
  letter-spacing: 0.4px;
}

/* <--- CardHeader Style ---> */
.card-header {
  width: 100%;
  position: relative;
}

/* <--- Card Style ---> */
.card {
  background-color: #fff;
  border-radius: 18px;
  box-shadow: 1px 1px 35px 12px rgba(0, 0, 0, 0.2);
  max-width: 320px;
  min-height: 100vh;
  margin: auto;
  overflow: hidden;
  position: relative;
}

/* <--- App Style ---> */
.container {
    min-height: 100vh;
    width: 100%;
    position: relative;
    padding: 20px;
}

@media (min-width:600px){
    .container{
        padding-top: 30px;
        padding-bottom: 30px;
    }
}
